.details header {
  background-color: red;
  color: white;
  padding: 3vw 0;
  position: relative;
  text-align: center;
}
.details header span {
  position: absolute;
  left: 2vw;
}
.details main {
  border-bottom: 5vw solid rgba(128, 128, 128, 0.3);
}
.details main .main-div {
  display: flex;
  padding: 3vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  margin-bottom: 2vw;
}
.details main .main-div img {
  width: 30vw;
  height: 20vw;
}
.details main .main-div div {
  flex: 1 1;
  margin-left: 1vw;
}
.details main .main-div div p:first-of-type {
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 2vw;
}
.details main .main-div div span {
  color: grey;
  padding: 0 4vw 0 0;
}
.details main .main-div div p:last-of-type {
  color: grey;
  margin: 1vw 0;
}
.details main ul {
  padding: 2vw 2vw 4vw 2vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}
.details main ul li {
  color: grey;
}
.details main .main-foot {
  padding: 2vw 0;
  justify-content: center;
  display: flex;
}
.details main .main-foot button {
  background-color: rgba(128, 128, 128, 0.3);
  outline: none;
  border: 1px solid grey;
  padding: 2vw 0;
  margin: 0 8vw;
  width: 30vw;
}
.details main .main-foot button:last-of-type:active {
  background-color: red;
}
.details article > p {
  font-size: 1.1rem;
  padding: 2vw 3vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  font-weight: bold;
}
.details article div {
  padding: 2vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}
.details article div p {
  margin: 1vw 0;
}
.details article div p:nth-of-type(1) {
  font-weight: bold;
}
.details article div p:nth-of-type(3) {
  color: grey;
}
.done {
  background-color: red !important;
  color: white;
}
