.user header {
  background-color: red;
  color: white;
  padding: 3vw 0;
  position: relative;
  text-align: center;
}
.user header span {
  position: absolute;
  left: 2vw;
}
.user > div {
  padding: 2vw;
  border-bottom: 5vw solid rgba(128, 128, 128, 0.3);
}
.user > div p {
  margin: 1vw;
  color: grey;
}
.user section {
  padding: 2vw;
}
.user section > p {
  font-size: 1.2rem;
  font-weight: bold;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  padding-bottom: 2vw;
}
.user section > div > div {
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}
.user section > div .section-div {
  display: flex;
  padding: 2vw;
}
.user section > div .section-div img {
  width: 30vw;
  height: 20vw;
  margin-right: 1vw;
}
.user section > div .section-div div {
  flex: 1 1;
}
.user section > div .section-div div:first-of-type {
  color: grey;
}
.user section > div .section-div div:last-of-type {
  display: flex;
  align-items: center;
  justify-content: center;
}
.user section > div .section-div div:last-of-type button {
  background-color: red;
  color: white;
  outline: none;
  border: none;
  padding: 1vw 5vw;
  font-size: 1rem;
}
.user section > div .appraise {
  display: none;
}
.user section > div .appraise textarea {
  width: 90vw;
  margin: 2vw;
  min-height: 20vw;
}
.user section > div .appraise span {
  margin: 0 3vw;
}
.user section > div .appraise div {
  display: flex;
  justify-content: center;
  margin: 2vw;
}
.user section > div .appraise div button {
  background-color: red;
  outline: none;
  border: none;
  padding: 1vw 5vw;
  margin: 0 5vw;
  color: white;
  font-size: 1rem;
}
