.search header {
  background-color: red;
  color: white;
  padding: 2vw 0;
  display: flex;
}
.search header input {
  flex: 1 1;
  height: 7vw;
  border-radius: 5vw;
  border: none;
  text-indent: 3vw;
  margin: 0 10vw 0 5vw;
}
.search header span {
  margin-left: 5vw;
}
.search .section2 h1 {
  font-size: 5vw;
  padding: 2vw 3vw;
  background-color: rgba(128, 128, 128, 0.3);
}
.search .section2 .section2-div {
  display: flex;
  margin: 3vw;
}
.search .section2 .section2-div img {
  width: 30vw;
  height: 20vw;
}
.search .section2 .section2-div div {
  flex: 1 1;
  position: relative;
  margin-left: 1vw;
}
.search .section2 .section2-div div p {
  margin-bottom: 1vw;
}
.search .section2 .section2-div div p:first-of-type {
  font-weight: bold;
}
.search .section2 .section2-div div p:nth-of-type(2) {
  font-size: 4vw;
}
.search .section2 .section2-div div p:last-of-type {
  color: red;
  font-weight: bold;
}
.search .section2 .section2-div div span {
  position: absolute;
  color: grey;
  font-size: 0.9rem;
}
.search .section2 .section2-div div span:first-of-type {
  right: 3vw;
  top: 0;
}
.search .section2 .section2-div div span:last-of-type {
  right: 3vw;
  bottom: 0;
}
