.chooseArea header {
  background-color: red;
  color: white;
  padding: 3vw 0;
  position: relative;
  text-align: center;
}
.chooseArea header span {
  position: absolute;
  left: 2vw;
}
.chooseArea section {
  display: flex;
  justify-content: center;
  padding: 8vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}
.chooseArea section span {
  font-size: 1.5rem;
  font-weight: bold;
}
.chooseArea nav p {
  font-weight: bold;
  margin: 3vw;
}
.chooseArea nav div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.chooseArea nav div button {
  background-color: white;
  border: 1px solid grey;
  padding: 2vw 6vw;
  margin: 1vw;
  width: 30vw;
  outline: none;
}
.chooseArea nav div button:active {
  background-color: red;
}
