.home header {
  background-color: red;
  color: white;
  padding: 2vw 0;
  display: flex;
}
.home header span {
  padding: 0 3vw;
  cursor: pointer;
}
.home header input {
  flex: 1 1;
  height: 7vw;
  border-radius: 5vw;
  border: none;
  text-indent: 3vw;
}
.home nav {
  margin-bottom: 3vw;
}
.home nav .nav-div {
  display: flex;
  flex-wrap: wrap;
  padding: 3vw 5vw;
  justify-content: center;
  color: grey;
}
.home nav .nav-div div {
  width: 15vw;
  padding: 3vw 1vw;
  text-align: center;
}
.home nav .nav-div div img {
  width: 15vw;
  height: 10vw;
}
.home nav .nav-div div span {
  position: relative;
  top: 1vw;
  font-size: 0.8rem;
}
.home nav .nav-pointer {
  text-align: center;
}
.home nav .nav-pointer input:first-of-type {
  background-color: red;
}
.home nav .nav-pointer input {
  width: 12px;
  height: 12px;
  background-color: rgba(128, 128, 128, 0.5);
  outline: none;
  border: none;
  border-radius: 50%;
  margin: 0 1vw;
}
.home .section1 {
  border-top: 5vw solid rgba(128, 128, 128, 0.3);
  margin-bottom: 3vw;
}
.home .section1 h1 {
  font-size: 5vw;
  padding: 2vw 3vw;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}
.home .section1 .section1-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 3vw 3vw;
}
.home .section1 .section1-div div {
  text-align: center;
  width: 29vw;
  display: flex;
  flex-flow: column;
  margin: 0 1vw;
}
.home .section1 .section1-div div p:first-of-type {
  font-size: 4vw;
}
.home .section1 .section1-div div p:last-of-type {
  font-size: 3vw;
  color: grey;
}
.home .section1 .section1-div div p {
  margin: 1vw 0;
}
.home .section1 .section1-div div img {
  width: 30vw;
  height: 20vw;
}
.home .section2 h1 {
  font-size: 5vw;
  padding: 2vw 3vw;
  background-color: rgba(128, 128, 128, 0.3);
}
.home .section2 .section2-div {
  display: flex;
  margin: 3vw;
}
.home .section2 .section2-div img {
  width: 30vw;
  height: 20vw;
}
.home .section2 .section2-div div {
  flex: 1 1;
  position: relative;
  margin-left: 1vw;
}
.home .section2 .section2-div div p {
  margin-bottom: 1vw;
}
.home .section2 .section2-div div p:first-of-type {
  font-weight: bold;
}
.home .section2 .section2-div div p:nth-of-type(2) {
  font-size: 4vw;
}
.home .section2 .section2-div div p:last-of-type {
  color: red;
  font-weight: bold;
}
.home .section2 .section2-div div span {
  position: absolute;
  color: grey;
  font-size: 0.9rem;
}
.home .section2 .section2-div div span:first-of-type {
  right: 3vw;
  top: 0;
}
.home .section2 .section2-div div span:last-of-type {
  right: 3vw;
  bottom: 0;
}
