.login header {
  background-color: red;
  color: white;
  padding: 3vw 0;
  position: relative;
  text-align: center;
}
.login header span {
  position: absolute;
  left: 2vw;
}
.login main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vw;
}
.login main div {
  display: flex;
  position: relative;
  flex-flow: column;
  width: 70vw;
}
.login main div * {
  margin: 1vw;
}
.login main div input {
  text-indent: 2vw;
  height: 7vw;
}
